// API file for making secure calls to the backend
const API_URL = 'http://127.0.0.1:8080/api/train-schedule';  // Update URL if backend is on another host

// Fetches train schedule from the backend API
export async function getTrainSchedule() {
    try {
        const response = await fetch(API_URL);  // Make GET request to the backend
        if (!response.ok) {
            throw new Error('Failed to fetch train schedule');
        }
        const data = await response.json();  // Convert response to JSON
        return data;  // Return the data to the caller
    } catch (error) {
        console.error('Error fetching train schedule:', error);
        return null;  // Handle error gracefully
    }
}

